.viewLeasedProperties_card {
    background-color: rgb(153, 122, 149);
    border-radius: 15px;
    padding: 20px;
    margin-bottom: 30px;
    background-image: linear-gradient(110deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.9) 50%, transparent 50%), url();
    background-size: 100%;
    border-radius: 3px;
    box-shadow: 0 1.5rem 4rem rgba(255, 155, 155, 0.2);
}

.viewLeasedProperties_btn {
    background-color: #001d43;
    color: white;
}

.viewLeasedProperties_btn:hover {
    background-color: #14498d;
    color: white;
}